import { io } from 'socket.io-client';

let socket = null; //   
const listeners = new Map(); //   

//  
export const initSocket = (token) => {
  if (!socket) {
    //    
    socket = io('http://localhost:3001', {
      auth: { token }, //  
      transports: ['websocket'], //   WebSocket
      reconnection: true, // 
      reconnectionAttempts: 5, //  5 
      reconnectionDelay: 3000 //    (3 )
    });

    //     
    socket.onAny((eventName, ...args) => {
      const handlers = listeners.get(eventName) || [];
      handlers.forEach(handler => handler(...args)); //   
    });
  }
  return socket;
};

//  
export const disconnectSocket = () => {
  if (socket) {
    socket.disconnect();
    socket = null;
    listeners.clear(); //   
  }
};

//   
export const onSocketEvent = (eventName, handler) => {
  const handlers = listeners.get(eventName) || [];
  listeners.set(eventName, [...handlers, handler]); //  
  
  //   
  return () => {
    const filtered = handlers.filter(h => h !== handler);
    listeners.set(eventName, filtered);
  };
};

//    
export const emitSocketEvent = (eventName, data) => {
  if (socket) {
    socket.emit(eventName, data); //  
  }
};

export const getSocket = ()=>{
  return socket; 
}